html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

/* Configuración general del flip-card */
.flip-card {
  perspective: 1200px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 400px;
  height: 570px;
  box-sizing: border-box;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}

.flip-card.flipped .flip-card-inner {
  transform: rotateY(180deg);
}

/* Estilos para cada cara de la tarjeta */
.flip-card-front, .flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  padding: 20px;
  background: rgba(255, 255, 255, 0.85);
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

.flip-card-back {
  transform: rotateY(180deg);
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes fade {
  0%, 100% {
    opacity: 0.3;
  }
  50% {
    opacity: 1;
  }
}

.google-button {
  margin-top: 1rem !important;
  background-color: #db4437 !important;
  color: white !important;
}

.google-button:hover {
  background-color: #c23321 !important;
}

/* Contenedor del video */
.video-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden; /* Oculta los bordes adicionales */
  z-index: -1;
}

/* Video de fondo */
.background-video {
  position: absolute;
  top: -10%; /* Aumenta el tamaño del video para evitar bordes visibles */
  left: -10%;
  width: 120%; /* Agranda el video más allá del contenedor */
  height: 120%;
  object-fit: cover; /* Asegura que el video se mantenga escalado correctamente */
  filter: blur(5px); /* Aplica el desenfoque */
}




