.carousel-container {
    position: relative;
    width: 100%; /* Ocupa el 100% del ancho de la pantalla */
    max-width: 100%; /* Asegura que no se desborde */
    margin: auto;
    overflow: hidden;
  }
  
  .carousel-images {
    display: flex;
    transition: transform 0.5s ease-in-out;
  }
  
  .carousel-image {
    min-width: 100%;
    height: 400px;
    object-fit: cover;
  }
  
  .carousel-arrow {
    position: absolute !important;
    top: 50% !important;
    transform: translateY(-50%);
    z-index: 10;
    background-color: rgba(255, 255, 255, 0.8) !important;
    border-radius: 50% !important;
  }
  
  .carousel-arrow:hover {
    background-color: rgba(255, 255, 255, 1);
  }
  
  .carousel-arrow-left {
    left: 16px;
  }
  
  .carousel-arrow-right {
    right: 16px;
  }
  
  .carousel-indicators {
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    gap: 8px;
  }
  
  .carousel-dot {
    padding: 0;
  }
  
  .carousel-dot-active {
    color: var(--mui-palette-primary-main);
  }
  
  .carousel-dot-inactive {
    color: gray;
  }
  
   /* Contenedor principal del carrusel */
.carrusel-container {
  position: relative;
  overflow: hidden;
  padding: 16px;
}

/* Botones de navegación (flechas izquierda y derecha) */
.carrusel-button {
  position: absolute !important;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
  background-color: white !important;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
}

.carrusel-button.left {
  left: 16px;
}

.carrusel-button.right {
  right: 16px;
}

/* Transición para el carrusel */
.carrusel-grid {
  display: flex !important;
  flex-wrap: nowrap !important; /* Mantiene las tarjetas en una sola fila */
  transition: transform 0.5s ease;
}

/* Tarjetas */
.carrusel-card {
  box-sizing: border-box;
  text-align: center;
  padding: 16px;
}