  /* Contenedor del texto y botón flotante */
  .floating-container {
    position: fixed;
    right: 16px;
    display: flex;
    align-items: center;
    z-index: 1000;
  }

.whatsapp-button {
    background-color: #25d366 !important;
    color: white !important;
    transition: transform 0.3s ease, background-color 0.3s ease !important;
    box-shadow: none !important;
  }
  
  .whatsapp-button.open {
    background-color: #25d366;
    transform: rotate(180deg); /* Rotación para animación */
  }
  
  .whatsapp-button:hover {
    background-color: #1da851 !important;
  }
  
  /* Récuadro flotante */
  .floating-box {
    position: fixed;
    right: 16px;
    width: 300px;
    background-color: white;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.2), 0px 0px 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    padding: 16px;
    z-index: 9999;
    opacity: 0;
    transform: translateY(20px); /* Desplazamiento inicial */
    animation: slide-in 0.3s forwards; /* Animación */
  }
  
  /* Animación de entrada */
  @keyframes slide-in {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  /* Cabecera verde */
  .header-box {
    background-color: #25d366;
    color: white;
    display: flex;
    align-items: center;
    padding: 8px 16px;
    border-radius: 8px;
    width: 100%;
    box-sizing: border-box;
    margin-bottom: 16px;
  }
  
  .whatsapp-logo {
    margin-right: 8px;
  }
  
  .header-title {
    font-weight: bold;
  }
  
  /* Inputs */
  .input-field {
    margin-bottom: 16px !important;
  }

/* Estilos para el texto */
.floating-text {
  margin-right: 8px !important;
  background-color: white;
  color: #000;
  font-family: Arial, sans-serif !important;
  font-size: 14px !important;
  font-weight: bold !important;
  padding: 2px 8px;
  border-radius: 8px;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.2), 0px 0px 10px rgba(0, 0, 0, 0.1);
}

  