/* Estilos del footer principal */
.footer {
    background-color: #29354b;
    padding: 30px;
    color: white !important;
  }
  
  .footer-title {
    margin-bottom: 16px !important;
    color: white;
  }
  
  .footer-input {
    margin-bottom: 16px !important;
    background-color: white;
    border-radius: 4px; /* 1 * 4px */
  }
  
  .footer-button {
    background-color: #e1306c !important;
  }
  
  .footer-button:hover {
    background-color: #c12054 !important;
  }
  
  /* Estilos de redes sociales */
  .footer-social {
    text-align: center;
  }
  
  .social-icon {
    margin: 8px !important;
    background-color: #1a1a1a !important;
    color: white !important;
    border-radius: 50% !important;
    width: 60px;
    height: 60px;
  }
  
  .social-icon:hover {
    background-color: #333333 !important;
  }
  
  .icon {
    font-size: 30px !important;
  }
  
  /* Derechos reservados */
  .footer-rights {
    background-color: #395f9b;
    text-align: center;
    padding: 8px !important;
  }
  
  .footer-rights-text {
    color: white;
  }