body{
    margin: 0;
}

/* Fondo de la barra de navegación */
.gamer-navbar {
    background: #29354b !important; /* Azul oscuro */
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.5);
    position: sticky;
    top: 0;
    z-index: 1000;
    margin-bottom: 0; /* Sin margen inferior */
  }

  /* Título */
.navbar-title {
  font-size: 1.5rem;
  font-weight: bold;
  color: #fff;
  text-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
}

/* Buscador centrado */
.navbar-search {
  display: flex;
  align-items: center;
  background-color: #fff;
  border-radius: 4px;
  width: 100%;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); 
}

.search-icon {
  padding: 0 10px;
  color: #333;
}

.search-input {
  color: #333;
  width: 100%;
}
  
  /* Título estilizado */
  .navbar-title {
    font-family: "Orbitron", sans-serif;
    font-weight: bold;
    color: #4db8ff; /* Azul brillante */
    text-shadow: 0 0 8px #4db8ff, 0 0 15px #4db8ff;
  }
  
  /* Menú horizontal */
  .menu-horizontal {
    display: flex;
    justify-content: center;
    gap: 2rem;
  }
  
  /* Ítems del menú */
  .menu-link {
    font-family: "Poppins", sans-serif;
    font-size: 1rem;
    text-decoration: none;
    padding: 0.5rem 1rem;
    border-radius: 8px;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }
  
  .menu-link:hover {
    color: #111f2a; /* Cambia a blanco al pasar el mouse */
    transform: scale(1.1);
    background: rgba(255, 255, 255, 0.2);
  }
  
  /* Menú móvil */
  .menu-item {
    font-family: "Poppins", sans-serif;
    font-size: 1rem;
    color: #b3cde0;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    transition: all 0.3s ease;
  }
  
  .menu-item:hover {
    color: #ffffff;
    background: rgba(255, 255, 255, 0.1);
  }
  
  /* Íconos */
  .menu-icon {
    color: #3b90b5;
    font-size: 1.2rem;
  }
  
  .menu-link:hover .menu-icon,
  .menu-item:hover .menu-icon {
    color: #ffffff;
  }
  
/* Línea animada multicolor con transición continua */

.animated-line {
    height: 4px;
    width: 100%;
    background: linear-gradient(90deg, red, orange, yellow, green, blue, indigo, violet, red, orange, yellow, green, blue, indigo, violet);
    background-size: 200%; /* Duplica el tamaño para hacer la transición suave */
    animation: move-line 10s linear infinite;
    position: absolute;
    margin: 0;
    top: 100%;
    left: 0;
  }

  .animated-line-footer {
    height: 4px;
    width: 100%;
    background: linear-gradient(90deg, red, orange, yellow, green, blue, indigo, violet, red, orange, yellow, green, blue, indigo, violet);
    background-size: 200%; /* Duplica el tamaño para hacer la transición suave */
    animation: move-line 10s linear infinite;
    margin: 0;
  }
  
  /* Animación continua */
  @keyframes move-line {
    0% {
      background-position: 0% 50%;
    }
    100% {
      background-position: 100% 50%;
    }
  }

  .user-avatar .MuiAvatar-root {
    width: 40px;
    height: 40px;
    cursor: pointer;
  }

  .user-avatar .MuiIconButton-root {
    padding: 0;
  }

  /* Estilo por defecto para los ítems del menú */
  .menu-link {
    text-decoration: none;
    color: #3b90b5;
    display: flex;
    align-items: center;
    gap: 5px;
    font-weight: bold;
  }

  /* Estilo para el ítem activo */
  .menu-active {
    color: #e7cda8;
  }

  .icon-active {
    color: #e7cda8; /* Mismo color que el texto activo */
  }

  .cart-icon .MuiBadge-badge {
    background-color: #e7cda8; /* Fondo dorado */
    color: black;           /* Texto en blanco */
  }
  
  .card {
    max-width: 345px;
    margin: auto;
    border-radius: 10px !important;
    transition: transform 0.3s;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.2);
  }
  
  .card:hover {
    box-shadow: 0px 0px 8px 2px #e7cda8;
    transform: scale(1.05);
  }
  
  .card-actions {
    display: flex;
    justify-content: center;
    gap: 16px;
    padding-bottom: 16px;
  }
  
  .icon-button-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .icon-button {
    font-size: 32px; /* Tamaño grande para los iconos */
  }
  
  .product-title {
    font-weight: bold;
  }
  
  .product-price {
    color: #1976d2; /* Color primario */
  }

.features-container {
  padding: 60px; /* Equivale a padding: 4 * 4px */
  background-color: #f9f9f9;
}

.feature-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.feature-icon {
  font-size: 50px; /* Si necesitas estilos adicionales para el ícono */
}

.feature-title {
  margin-top: 8px; /* Equivale a marginTop: 2 * 4px */
  font-weight: bold;
  word-wrap: break-word;
}

.feature-description {
  margin-top: 4px;
  color: #6c757d; /* Cambia el color según la preferencia */
  word-wrap: break-word;
  max-width: 300px;
}

.navBar-floating-container {
  position: fixed;
  bottom: 0px;
  display: flex;
  align-items: center;
  z-index: 1000;
  width: 100%;
  background: #29354b !important;
}


  
  
  